import "./index.scss"
import React, { Component } from "react"
import Head from "~/components/head"
import { WingBlank, SearchBar } from "antd-mobile"
import { connect } from "react-redux"
import { getSearchList } from "../../react-redux/actions";
import { history } from "&"
import search from "@/assets/images/search.jpg"
import {
    Link
} from "react-router-dom"

@connect(
    state => {
        return {
            searchList: state.getIn(["search", "searchList"])
        }
    }
)

class Search extends Component {
    // componentDidMount() {
    // }
    handleSubmit(keyword) {
        // console.log(keyword)
        this.props.dispatch(getSearchList({
            url: "/react/getGoodList",
            params: {
                keyword: keyword
            }
        }))
        this.value = "gaoyinghua"


    }
    // goToDetail = (goodsId) => {
    //     history.push(`/main/detail/goodsId=${goodsId}?goodsName=${}`);

    // }

    render() {
        const {
            searchList
        } = this.props

        return (
            <div>
                <Head title="搜索" search={true} show={true}></Head>
                <WingBlank>
                    <SearchBar className="search" placeholder="请输入关键字" onSubmit={this.handleSubmit.bind(this)} />
                    <div className="box1">
                        <ul>

                            {searchList &&
                                searchList.map((item, i) => (
                                    <Link key={i} to={`/main/detail/ ${item.item_id} ?goodsName=${item.item_name}`}>
                                        <li className="fadeIn-in" >


                                            <div className="div1"><img src={item.over_image_url} alt="" /></div>
                                            <div className="div2">
                                                <p className="p1">{item.item_long_name}</p>
                                                <p className="p2">{item.item_short_name}</p>
                                                <p className="p3">￥{item.min_price / 100}</p>
                                            </div>
                                        </li>
                                    </Link>

                                ))
                            }
                            {
                                !searchList &&
                                <div style={{ textAlign: "center", marginTop: "3rem" }}>推荐您搜索舒客 手链</div>

                            }

                        </ul>
                    </div>

                </WingBlank>
            </div >
        )
    }
}
export default Search